/* jshint esversion: 6 */
import external from '../externalModules.js';
import { getToolState } from '../stateManagement/toolState.js';
import loadHandlerManager from '../stateManagement/loadHandlerManager.js';

// This function causes any scrolling actions within the stack to propagate to
// All of the other viewports that are synced
export default function (synchronizer, sourceElement, targetElement, eventData) {
  const cornerstone = external.cornerstone;
  const $ = external.$;
  // Get the overlays
  const parent = $(targetElement).parent();

  const childDivs = $(parent).find('.overlay');
  const topLeft = $(childDivs[0]).find('div');
  const topRight = $(childDivs[1]).find('div');
  const bottomLeft = $(childDivs[2]).find('div');
  const bottomRight = $(childDivs[3]).find('div');

  // If the target and source are the same, stop
  if (sourceElement === targetElement) {
    return;
  }

  // If there is no event, or direction is 0, stop
  if (!eventData || !eventData.direction) {
    return;
  }
  // Get the stack of the target viewport
  const stackToolDataSource = getToolState(sourceElement, 'stack');
  const stackToolDataTarget = getToolState(targetElement, 'stack');

  if (!stackToolDataSource || !stackToolDataTarget) {
    return;
  }
  const stackDataSource = stackToolDataSource.data[0];
  const stackDataTarget = stackToolDataTarget.data[0];


  //  Get the new index for the stack
  const newImageIdIndex = eventData.newImageIdIndex + (stackDataTarget.layoutIndex - stackDataSource.layoutIndex);

  if (newImageIdIndex > stackDataTarget.imageIds.length - 1) {
    stackDataTarget.currentImageIdIndex = newImageIdIndex;
    $(targetElement).hide();

    if (childDivs.text().length > 0) {
      topLeft.hide();
      topRight.hide();
      bottomLeft.hide();
      bottomRight.hide();
    }
    let ol = $(targetElement).parent().find('.overlay-text');

    if (ol.length < 1) {
      ol = $('<div class="overlay overlay-text">NO IMAGE</div>').appendTo($(targetElement).parent()).show();
    }
    const ow = $(targetElement).parent().width() / 2;

    const oh = $(targetElement).parent().height() / 2;

    ol.css({ top: oh,
      left: ow - ol.width() / 2 });

    return;
  }

  $(targetElement).show();
  $(targetElement).parent().find('.overlay-text').remove();
  topLeft.show();
  topRight.show();
  bottomLeft.show();
  bottomRight.show();
  // If the index has not changed, stop here
  if (stackDataTarget.currentImageIdIndex === newImageIdIndex) {
    return;
  }

  const startLoadingHandler = loadHandlerManager.getStartLoadHandler();
  const endLoadingHandler = loadHandlerManager.getEndLoadHandler();
  const errorLoadingHandler = loadHandlerManager.getErrorLoadingHandler();

  if (startLoadingHandler) {
    startLoadingHandler(targetElement);
  }

  let loader;

  if (!stackDataTarget.imageIds[newImageIdIndex]) {
    return;
  }


  if (stackDataTarget.preventCache === true) {
    loader = cornerstone.loadImage(stackDataTarget.imageIds[newImageIdIndex]);
  } else {
    loader = cornerstone.loadAndCacheImage(stackDataTarget.imageIds[newImageIdIndex]);
  }

  loader.then(function (image) {
    const viewport = cornerstone.getViewport(targetElement);

    stackDataTarget.currentImageIdIndex = newImageIdIndex;
    synchronizer.displayImage(targetElement, image, viewport);
    if (endLoadingHandler) {
      endLoadingHandler(targetElement, image);
    }
  }, function (error) {
    const imageId = stackDataTarget.imageIds[newImageIdIndex];

    if (errorLoadingHandler) {
      errorLoadingHandler(targetElement, imageId, error);
    }
  });
}



