/* jshint esversion: 6 */
import external from '../externalModules.js';
import { getToolState } from '../stateManagement/toolState.js';
import loadHandlerManager from '../stateManagement/loadHandlerManager.js';
import calculateAutomaticPlanePositioning from '../referenceLines/calculateAutomaticPlanePositioning.js';

// This function causes the image in the target stack to be set to the one closest
// To the image in the source stack by image position
export default function (synchronizer, sourceElement, targetElement) {

  // Ignore the case where the source and target are the same enabled element
  if (targetElement === sourceElement) {
    return;
  }

  const cornerstone = external.cornerstone;
  const targetStackToolDataSource = getToolState(targetElement, 'stack');
  const targetStackData = targetStackToolDataSource.data[0];


  const sourceImage = cornerstone.getEnabledElement(sourceElement).image;
  const targetImage = cornerstone.getEnabledElement(targetElement).image;

  // Make sure the images are actually loaded for the target and reference
  if (!sourceImage || !targetImage) {
    return;
  }

  const sourceImagePlane = cornerstone.metaData.get('imagePlaneModule', sourceImage.imageId);
  const targetImagePlane = cornerstone.metaData.get('imagePlaneModule', targetImage.imageId);

  // Make sure the target and reference actually have image plane metadata
  if (!sourceImagePlane ||
      !targetImagePlane ||
      !sourceImagePlane.pixelSpacing ||
      !targetImagePlane.pixelSpacing ||
      !sourceImagePlane.rows ||
      !sourceImagePlane.columns ||
      !sourceImagePlane.rowCosines ||
      !sourceImagePlane.columnCosines ||
      !sourceImagePlane.imagePositionPatient ||
      !targetImagePlane.rowCosines ||
      !targetImagePlane.columnCosines ||
      !targetImagePlane.imagePositionPatient) {
    return;
  }

  // The image planes must be in the same frame of reference
  if (sourceImagePlane.frameOfReferenceUID !== targetImagePlane.frameOfReferenceUID) {
    return;
  }


  const automaticPlanePositioning = calculateAutomaticPlanePositioning(targetStackData, sourceImagePlane, targetImagePlane);

  if (automaticPlanePositioning === null || automaticPlanePositioning === undefined) {
    cornerstone.updateImage(targetElement);

    return;
  }


  // Get the new index for the stack
  let newImageIdIndex = automaticPlanePositioning;

  // Clamp the index
  newImageIdIndex = Math.min(Math.max(newImageIdIndex, 0), targetStackData.imageIds.length - 1);

  // Do nothing if the index has not changed
  if (newImageIdIndex === targetStackData.currentImageIdIndex) {
    return;
  }

  const startLoadingHandler = loadHandlerManager.getStartLoadHandler();
  const endLoadingHandler = loadHandlerManager.getEndLoadHandler();
  const errorLoadingHandler = loadHandlerManager.getErrorLoadingHandler();

  if (startLoadingHandler) {
    startLoadingHandler(targetElement);
  }

  let loader;

  if (targetStackData.preventCache === true) {
    loader = cornerstone.loadImage(targetStackData.imageIds[newImageIdIndex]);
  } else {
    loader = cornerstone.loadAndCacheImage(targetStackData.imageIds[newImageIdIndex]);
  }

  loader.then(function (image) {
    const viewport = cornerstone.getViewport(targetElement);

    targetStackData.currentImageIdIndex = newImageIdIndex;
    synchronizer.displayImage(targetElement, image, viewport);
    if (endLoadingHandler) {
      endLoadingHandler(targetElement, image);
    }
  }, function (error) {
    const imageId = targetStackData.imageIds[newImageIdIndex];

    if (errorLoadingHandler) {
      errorLoadingHandler(targetElement, imageId, error);
    }
  });
}



